﻿import { VertexAuth } from "@vertx/auth"
import { jsx, JSXElement } from "@vertx/jsx"
import * as Tone from "tone"
import { MasterBus } from "./components/Channel";
import { Config } from "./components/Config";
import { GroupManager, Pad, PadGroup, PadManager, SamplePad } from "./components/samplePad"
import { ResourceBrowser, ResourceItem } from "./views/resourceBrowser/resourcebrowser";
export const packageJson = require('../package.json');



window.AudioContext = window.AudioContext || window.AudioContext;
export type RightContent = "Explorer" | "Context"

export module Pads {

    export class Application {
        constructor() { }

        static config = new Config();
        auth:VertexAuth;
        stackHost:string;
        private _redirectURI:string;;
        private _clientID:string;

        // selectedPad:number;
        static contextContainer = document.getElementById('context-container');
        static contextToggle = document.getElementById("context-button-show");
        static contextWrapper = document.getElementById("context-wrapper");
        static browserToggle = document.getElementById("resource-button-show");
        static browserWrapper = document.getElementById("browser-wrapper");
        static loadingCover = document.getElementById("loading-cover")


        static masterChannel:Tone.Channel
        static masterFFT:Tone.FFT;
        static activePad:Pad
        static activeGroup:PadGroup;


        async initVertex() {

            this._clientID = "1212a395-b812-42de-a5c5-3fcace6b5c80";
            this._redirectURI = "http://localhost:3000/";
            this.stackHost = "vertx.cloud"
            Vertex.Globals.vertexStackUrl = this.stackHost;


            this.auth = new VertexAuth();
    
            await this.auth.initAsync({
                vertxStackHost: this.stackHost,
                redirectUri: this._redirectURI,
                clientId: this._clientID,
            });
        
            // User
            const user = await this.auth.getUserAsync();
            const isSignedIn = !!user && !user.expired;
        
            // User is not signed in, redirect to login
            if (!isSignedIn) {
                await this.auth.redirectToSignIn({
                    mode: "prompt"
                })
            }
    
            // User is logged in, start the app
            else {
                let spaceId = setSpaceIdFromParameters();
                this.initVertex(user,spaceId);
            }

            Vertex.Globals.bearerToken = user.access_token;
            Vertex.Globals.spaceId = spaceId;
            Vertex.Globals.event.on("vertex:registerComponentSystems", async (componentSystem: Vertex.NodeComponentModel.ComponentSystem) => {
                //TODO:: Register component systems 
                //EG. componentSystem.register(new MaterialsTabComponentSystem());
                Vertex.Globals.event.fire("vertex:componentRegistrationComplete");
            })
        }

        drawSythPadLegacy() {

            let mainContainer = document.getElementById('main-container');

            let OCTAVE = "3"
            let UPPER = (parseInt(OCTAVE) + 1).toString();
            let notes = [`C${OCTAVE}`,`C#${OCTAVE}`,`D${OCTAVE}`,`D#${OCTAVE}`,`E${OCTAVE}`,`F${OCTAVE}`,`F#${OCTAVE}`,`G${OCTAVE}`,`G#${OCTAVE}`,`A${OCTAVE}`,`A#${OCTAVE}`,`B${OCTAVE}`,`C${UPPER}`,`C#${UPPER}`,`D${UPPER}`,`D#${UPPER}`];
            let inverse = notes.reverse();
            
            let padArray = <div id="pad-array" class="pad-array"></div> as HTMLElement;
            for (let i=0;i<inverse.length;i++) {

                let note = inverse[i];
                let pad = <button class="pad" data-note={note}>{note}</button> as HTMLButtonElement
                let synth = new Tone.Synth().toDestination();
                let isPlaying:boolean = false;
                let now = Tone.now();
                pad.onmousedown = () => {
                    synth.triggerAttack(note, now);
                    isPlaying = true;
                    pad.classList.add('active');
                }
                pad.onmouseup = () => {
                    synth.triggerRelease();
                    isPlaying = false;  
                    pad.classList.remove('active');
                }
                pad.addEventListener
                padArray.appendChild(pad);
            }
            mainContainer.prepend(padArray);
        }

        // openPadContext(pad:number) {

        //     this.selectedPad = pad;

        // }

        setVersionNumber() {

            let versionElement = document.getElementById("version-number");
            let v = "v-" + packageJson.version;
            versionElement.innerText = v;

        }

        static showLoading() {

            Pads.Application.loadingCover.classList.remove("d-none");

        }
        static hideLoading() {

            Pads.Application.loadingCover.classList.add("d-none");

        }

        static toggleRightContent(container:RightContent) {

            if (container == "Context") {
                Pads.Application.contextToggle.classList.remove('inactive');
                Pads.Application.browserToggle.classList.add('inactive');
                Pads.Application.contextWrapper.classList.remove("d-none");
                Pads.Application.browserWrapper.classList.add("d-none");
            }
            if (container == "Explorer") {
                Pads.Application.contextToggle.classList.add('inactive');
                Pads.Application.browserToggle.classList.remove('inactive');
                Pads.Application.contextWrapper.classList.add("d-none");
                Pads.Application.browserWrapper.classList.remove("d-none");
            }

        }

        async init():Promise<void> {

            let bus =  new MasterBus()
            Pads.Application.masterChannel = bus.init();
            Pads.Application.masterFFT = bus.FFT;
            let parent = document.getElementById('main-container');
            

            Pads.Application.contextToggle.addEventListener('click',() => {
                Pads.Application.toggleRightContent("Context")
            })
            Pads.Application.browserToggle.addEventListener('click',() => {
                Pads.Application.toggleRightContent("Explorer");
            })

            console.warn("Init Main")
            console.error("Init Main");
            let browser = <ResourceBrowser activePad={Pads.Application.activePad}></ResourceBrowser>
            console.log(browser);

            let groupManager = new GroupManager(parent);
            let isReady = await groupManager.init();
            if (isReady) {
                groupManager.selectGroup(groupManager.padGroups[0]);
            }
            
        }
    }
}

function setSpaceIdFromParameters() {
    var regex = /worldId=(.*)/;
    var result = regex.exec(window.location.href);
    var guid = result[1];
    Vertex.Globals.spaceId = guid;
    return guid;
}

const application = new Pads.Application();
application.setVersionNumber();
application.init();