import { jsx, JSXComponent } from "@vertx/jsx";
import { Pad, PadGroup } from "../../components/samplePad";
let css = require('../contextMenu/context.css')

export type ContextItem = "PadGroup" | "Pad"

export class ContextMenu implements JSXComponent {

  event: MouseEvent | undefined;
  menu: HTMLMenuElement | undefined;
  item: PadGroup | Pad | undefined;
  type: ContextItem | undefined | null;

  getType() {

    switch (this.item.type as ContextItem) {
      case "PadGroup":
        this.type = "PadGroup"
        this.setupGroup();
        break;
      case "Pad":
        this.type = "Pad";
        this.setupPad();
        break;
      default:
        return <div></div> as HTMLElement;
    }

  }

  setupGroup() {
    console.warn("Group");
    return <div></div> as HTMLElement
  }
  setupPad() {
    console.warn("Pad");
    return <div></div> as HTMLElement
  }

  mute() {

    console.warn("Muting..")
    if (this.type == "Pad") {
      let item = this.item as Pad;
      let isMuted = item.channel.muted;
      item.channel.mute = !isMuted;
      return;
    }
    if (this.type == "PadGroup") {

      let item = this.item as PadGroup;
      let isMuted = item.channel.muted;
      item.channel.mute = !isMuted;
      // Implement groups. We need to go up the tree and figure out which group has been muted.
      
    }


  }

  showMenu(x, y) {
    this.menu.style.left = x + 'px';
    this.menu.style.top = y + 'px';
    this.menu.classList.add('show-menu');
  }

  render() {

    console.log("Render Context")
    this.menu = <menu class="menu">
      <li class="menu-item">
        <button type="button" class="menu-btn"> <i class="fa fa-folder-open"></i> <span class="menu-text" onclick={() => { this.mute() }}>Mute</span> </button>
      </li>
    </menu> as HTMLMenuElement
    let options = this.getType() as HTMLElement;
    if (options) {
      this.menu.prepend(options)
    }
    document.body.prepend(this.menu);
    this.showMenu(this.event.pageX, this.event.pageY);
    document.addEventListener('click', () => {
      this.menu.remove();
      document.removeEventListener('click', () => {this});    
    });


    return this.menu;
  }

}