import { jsx, JSXComponent, JSXElement } from "@vertx/jsx";
import { Pad, PadGroup, SamplePad } from "../../components/samplePad";
import * as Tone from "tone"
import { Pads } from "../../main";
import { Notifications, Utils } from "../../components/Utils";
let css = require('../contextMenu/context.css');
import * as files from '../../resources/*.mp3';
import * as phrixDrums from "../../resources/phrixus/drums/*.wav";
import * as phrixBass from "../../resources/phrixus/bass/*.wav";
import * as PhrixTex from "../../resources/phrixus/texture/*.wav"

export interface ResourceObject {
    name:string,
    path:string,
    tags:string[];
}

export class ResourceBrowser implements JSXComponent {

    wrapper: HTMLElement = document.getElementById("browser-wrapper");

    render(children: JSXElement[]): JSXElement {

        console.warn("Rendering Resource Browser");
        console.warn("Rendering Browser");

        let resourceURLs = Object.values(files);
        let resourceNames = Object.keys(files);

        let resourceObjects:ResourceObject[] = []

        for (let i=0;i<resourceURLs.length;i++) {

            let object = {
                "name":resourceNames[i],
                "path":resourceURLs[i],
                "tags":["bork"]
            } as ResourceObject
            resourceObjects.push(object);

        }

        let phrixDrumsUrl = Object.values(phrixDrums);
        let phrixDrumsNames = Object.keys(phrixDrums);
        for (let i=0;i<phrixDrumsUrl.length;i++) {

            let object = {
                "name":phrixDrumsNames[i],
                "path":phrixDrumsUrl[i],
                "tags":["phrixus","drums"]
            } as ResourceObject
            resourceObjects.push(object);

        }

        
        let phrixBassUrl = Object.values(phrixBass);
        let phrixBassNames = Object.keys(phrixBass);
        for (let i=0;i<phrixDrumsUrl.length;i++) {

            let object = {
                "name":phrixBassNames[i],
                "path":phrixBassUrl[i],
                "tags":["phrixus","bass"]
            } as ResourceObject
            resourceObjects.push(object);

        }

        let phrixTexUrl = Object.values(PhrixTex);
        let phrixTexNames = Object.keys(PhrixTex);
        for (let i=0;i<phrixDrumsUrl.length;i++) {

            let object = {
                "name":phrixTexNames[i],
                "path":phrixTexUrl[i],
                "tags":["phrixus","texture"]
            } as ResourceObject
            resourceObjects.push(object);

        }


        let browser = <div></div>
        console.log("Resource Objects:",resourceObjects);
        resourceObjects.forEach(item => {
            let resourceItem = <ResourceItem name={item.name} path={item.path} tags={item.tags}></ResourceItem>
            browser.appendChild(resourceItem);
        })


        this.wrapper.append(browser);
        return browser

    }
}

export class ResourceItem implements JSXComponent {

    name:string;
    path:string;
    url: URL;
    tags:string[];

    render() {

        // let item;
        this.url = new URL(this.path, import.meta.url);

        let tagContainer = <div class="resource-tags-container"></div>

        this.tags.forEach(tag => {
            let tagItem = <div class="resource-tag">{tag}</div>
            tagContainer.appendChild(tagItem)
        })

        let loadButton = <button type="button" class="mr-2"> Load</button>
        let item = <div class="resource-item d-flex w-100 justify-between align-center">
                        <div class="resource-name">
                            {this.name}
                        </div>
                        <div class="resource-player">
                            <audio controls>
                                <source src={this.url}></source>
                            </audio>
                        </div>
                        {tagContainer}
                        {loadButton}
                    </div>
        loadButton.addEventListener('click', () => {

            if (!Pads.Application.activePad) {
                Notifications.Error.show("Please select a pad.")
                return;
            }

            //this.createFile();

            if (Pads.Application.activePad.activeComponent) {
                Pads.Application.activePad.removeActiveComponent(Pads.Application.activePad)
            }

            Pads.Application.showLoading();
            Utils.getFileFromUrl(this.url, this.name).then((file) => {

                let sampler = new SamplePad(Pads.Application.activePad, Pads.Application.activePad.channel);
                sampler.addComponent(Pads.Application.activePad.HTMLElement, Pads.Application.activePad.activeComponentContext);
                Pads.Application.activePad.activeComponent = sampler;
                Pads.Application.activePad.isReady = true;
                sampler.registerFileToPlayer(file).then(() => {
                    Pads.Application.activePad.activeComponent.isReady = true;
                    console.warn("Context:", Pads.Application.activePad.padContext);
                    Pads.Application.toggleRightContent("Context");
                    Pads.Application.activePad.renderContext();
                })

            });



        })


        return item
    }

}