export class SynthPad {

    type:string = "Synth"
    isReady:boolean = false;

    init(padElement:HTMLElement) {

        let mainContainer = document.getElementById('main-container');

        let OCTAVE = "3"
        let UPPER = (parseInt(OCTAVE) + 1).toString();
        let notes = [`C${OCTAVE}`,`C#${OCTAVE}`,`D${OCTAVE}`,`D#${OCTAVE}`,`E${OCTAVE}`,`F${OCTAVE}`,`F#${OCTAVE}`,`G${OCTAVE}`,`G#${OCTAVE}`,`A${OCTAVE}`,`A#${OCTAVE}`,`B${OCTAVE}`,`C${UPPER}`,`C#${UPPER}`,`D${UPPER}`,`D#${UPPER}`];
        let inverse = notes.reverse();
        
        let padArray = <div id="pad-array" class="pad-array"></div> as HTMLElement;
        for (let i=0;i<inverse.length;i++) {

            let note = inverse[i];
            let pad = <button class="pad" data-note={note}>{note}</button> as HTMLButtonElement
            let synth = new Tone.Synth().toDestination();
            let isPlaying:boolean = false;
            let now = Tone.now();
            pad.onmousedown = () => {
                synth.triggerAttack(note, now);
                isPlaying = true;
                pad.classList.add('active');
            }
            pad.onmouseup = () => {
                synth.triggerRelease();
                isPlaying = false;  
                pad.classList.remove('active');
            }
            pad.addEventListener
            padArray.appendChild(pad);
        }
        mainContainer.prepend(padArray);
        this.renderContext();

    }

    play():void {

    }
    stop():void {

    }
 
    renderContext():void {
        console.log("Synth Yeet")
    }
    getContext():any {

    }

}

// Sampler components to be bound to pad.
export class SamplePad implements Playable {

    type:string = "Sampler";
    isReady:boolean = false;

    pad:Pad;
    player:Tone.Player;
    currentFile:File;
    uploadButton:HTMLElement;
    HTMLElement:HTMLElement;
    padContext:HTMLElement;

    channel:Tone.Channel // Send

    sliceStart:number = 0;
    loopStart:number;
    startTime:number = 0;

    constructor(pad,channel) {
        this.pad = pad;
        this.channel = channel;
    }

    // To Implement
    loop;
    loopPoints
    pitchShift

    clone <T>(instance: T): T {
        const copy = new (instance.constructor as { new (): T })();
        Object.assign(copy, instance);
        return copy;
    }

    renderContext():void {

        let contexts = document.querySelectorAll('.pad-context');
        contexts.forEach(context => {
            context.classList.add('d-none');
        })
        this.padContext.classList.remove('d-none');
        if (this.isReady) {
            return;
        }

        // if (this.isReady) {
        //     this.drawSampler(this.player);
        //     this.pad.initEffectsStack(this.player);
        // }

    }

    getContext():any {

        let clone = this.clone(this);
        return clone;

    }

    bindPad() {

        // [todo] This can probably now be moved to pad



    }

    unbindPad() {

        this.HTMLElement.onmousedown = () => {}
        this.HTMLElement.onmouseup = () => {}

    }

    async registerFileToPlayer(file:File) {

        this.currentFile = file;
        let audio = URL.createObjectURL(file); 
        Pads.Application.showLoading();

        // let player = new Tone.Player({
        //     url:audio,
        //     loop:true
        // }).connect(this.channel);
        
        let player = new Tone.Player({
            url:audio,
        }).connect(this.channel);

        Tone.loaded().then(async (tone) => {
            this.player = player;
            await this.drawSampler(this.player,this.padContext);
            if (!this.pad.effectsStack) {
                this.pad.initEffectsStack(this.player)
            }
            this.HTMLElement.classList.add('loaded');
            let content = this.HTMLElement.querySelector(".content");
            content.innerHTML = "";
            let name = <h3 class="pad-content-name">{this.currentFile.name}</h3>
            content.prepend(name)
            this.isReady = true;
        })

    }

    play() {

        this.player.start(0,this.startTime);
        // Tone.context.resume().then(() => {
        //     console.log("Ever get here?")
        //     Tone.Transport.start();
        // })

    }

    stop() {

        this.player.stop();

    }

    async drawSampler(player,padContext) {
        
        let self = this;
        let color = Utils.getColorValue(this.pad.color)
        let canvasHolder = <div></div>

        // [todo] this is gross move again, okay for now since we only do sampler.
        let unloadActiveComponentButton = <button class={`default-button ${this.pad.color}`}>Unload Component</button>
        this.padContext.prepend(unloadActiveComponentButton)
        unloadActiveComponentButton.addEventListener('click',() => {
            this.pad.removeActiveComponent(this.pad)
        })

        let componentContainer = <ComponentContainer name="Sampler Component" color={color}>{unloadActiveComponentButton}{canvasHolder}</ComponentContainer> as HTMLElement
        this.padContext.append(componentContainer)

        WaveForm.SoundCloudWaveform.generate(this.currentFile, {
            onComplete: function(png, pixels) {
                let canvas = document.createElement('canvas');
                let title = <h3>Kimbo <span class={self.pad.color}>Slice</span></h3>
                canvasHolder.appendChild(title);
                canvasHolder.appendChild(canvas);
                let context = canvas.getContext('2d');
                let canvasW = canvas.getBoundingClientRect().width;
                let canvasH = canvas.getBoundingClientRect().height;

                context.putImageData(pixels, 0, 0);
                context.fillStyle = color + '60'
                context.fillRect(self.sliceStart,0,canvasW,canvasH);   

                canvas.addEventListener('mousedown',(e) => {
                    let position = Utils.getCursorPosition(canvas,e);
                    let length = player.buffer.length * player.sampleTime;
                    let value = Utils.getScaledValue(position,0,canvasW,0,length)

                    context.fillStyle = color + '60'
                    context.clearRect(0,0,canvas.width,canvas.height);
                    context.putImageData(pixels, 0, 0);
                    context.fillRect(position,0,canvasW - position,canvasH);
                    self.sliceStart = position;

                    self.startTime = value.toFixed(5);
                    // self.loopStart = fixedValue
                    // player.loopStart = fixedValue;
                    //self.player.sync().start(self.sliceStart)
                    self.play();
                    //player.start();
                })
                canvas.addEventListener('mouseup',() => {
                    player.stop();
                })
                self.isReady = true;
                Pads.Application.hideLoading();
            }
        });
    }

    addComponent(htmlElement,padContext) {

        this.padContext = padContext
        this.HTMLElement = htmlElement;
        this.bindPad();
        //this.renderContext();

    }

    

}